/* ========================================= */
/* CALENDAR OVERLAY - CLICKABLE CENTER SCREEN */
/* ========================================= */

.calendar-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: 1000 !important; /* Higher z-index */
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  padding: var(--space-4) !important;
  animation: center-modal-fade var(--transition-slow) !important;
}

@keyframes center-modal-fade {
  from { 
    opacity: 0; 
  }
  to { 
    opacity: 1; 
  }
}

.overlay-backdrop {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background-color: rgba(0, 0, 0, 0.5) !important; /* Darker for visibility */
  cursor: pointer !important; /* Clickable to close */
}

.calendar-modal {
  background-color: var(--color-surface-elevated) !important;
  border-radius: var(--radius-lg) !important;
  padding: var(--space-6) !important;
  width: 100% !important;
  max-width: 350px !important;
  max-height: 80vh !important;
  box-shadow: var(--shadow-xl) !important;
  position: relative !important;
  z-index: 1001 !important;
  animation: center-modal-scale var(--transition-slow) !important;
  border: 2px solid var(--color-border) !important;
  overflow: visible !important;
}

@keyframes center-modal-scale {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.modal-header {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  margin-bottom: var(--space-4) !important;
  padding-bottom: var(--space-2) !important;
  border-bottom: 1px solid var(--color-divider) !important;
}

.modal-title {
  font-family: var(--font-family-display) !important;
  font-size: var(--text-lg) !important;
  font-weight: var(--font-weight-bold) !important;
  color: var(--color-text-primary) !important;
}

.calendar-navigation {
  display: flex !important;
  justify-content: space-between !important;
  align-items: center !important;
  margin-bottom: var(--space-3) !important;
}

.nav-button {
  background: var(--color-surface) !important;
  border: 1px solid var(--color-border) !important;
  font-size: 18px !important;
  color: var(--color-primary) !important;
  cursor: pointer !important;
  padding: var(--space-2) !important;
  border-radius: var(--radius) !important;
  transition: all var(--transition-base) !important;
  width: 36px !important;
  height: 36px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.nav-button:hover {
  background-color: var(--color-primary) !important;
  color: white !important;
  transform: scale(1.1) !important;
}

.calendar-grid {
  display: grid !important;
  grid-template-columns: repeat(7, 1fr) !important;
  gap: 4px !important;
}

.calendar-cell {
  aspect-ratio: 1 !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  border-radius: var(--radius) !important;
  font-size: var(--text-base) !important;
  font-weight: var(--font-weight-medium) !important;
  cursor: pointer !important;
  transition: all var(--transition-fast) !important;
