  border-radius: var(--radius-md);
  padding: var(--space-4);
  border: 1px solid var(--color-border);
  text-align: center;
  box-shadow: var(--shadow-xs);
}

.total-count {
  font-family: var(--font-family-display);
  font-size: var(--text-base);
  font-weight: var(--font-weight-bold);
  color: var(--color-text-primary);
  margin-bottom: var(--space-1);
}

.limit-warning {
  font-size: var(--text-sm);
  font-weight: var(--font-weight-medium);
  color: var(--color-warning);
  line-height: 1.3;
}

/* TRAVEL CLASS SECTION */
.class-section {
  border-top: 1px solid var(--color-divider);
  padding-top: var(--space-5);
}

.class-title {
  font-family: var(--font-family-display);
  font-size: var(--text-xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text-primary);
  margin-bottom: var(--space-5);
  letter-spacing: -0.02em;
}

.class-options {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--space-3);
}

.class-option {
  padding: var(--space-4);
  border: 1.5px solid var(--color-border);
  background-color: var(--color-surface-elevated);
  border-radius: var(--radius-md);
  cursor: pointer;
  font-size: var(--text-sm);
  font-weight: var(--font-weight-semibold);
  color: var(--color-text-primary);
  transition: all var(--transition-base);
  text-align: center;
  box-shadow: var(--shadow-xs);
  min-height: var(--tap-target-min);
  display: flex;
  align-items: center;
  justify-content: center;
}

.class-option.active {
  border-color: var(--color-primary);
  background: linear-gradient(135deg, rgba(37, 99, 235, 0.1) 0%, rgba(37, 99, 235, 0.05) 100%);
  color: var(--color-primary);
  font-weight: var(--font-weight-bold);
  box-shadow: var(--shadow-focus);
  transform: translateY(-1px);
}

.class-option:hover:not(.active) {
  border-color: var(--color-primary);
  background-color: rgba(37, 99, 235, 0.05);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

/* ========================================= */
/* GROUP BOOKING MODAL - PROFESSIONAL */
/* ========================================= */

.group-booking-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: overlay-fade var(--transition-slow);
}

.group-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-overlay);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.group-modal {
  background-color: var(--color-surface-elevated);
  border-radius: var(--radius-xl);
  padding: var(--space-8);
  margin: var(--space-4);
  max-width: 400px;
  width: calc(100% - 32px);
  box-shadow: var(--shadow-xl);
  position: relative;
  z-index: 401;
  animation: modal-appear var(--transition-slow);
  border: 1px solid var(--color-border);
}

.group-modal h3 {
  font-family: var(--font-family-display);
  font-size: var(--text-xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text-primary);
  margin-bottom: var(--space-3);
  letter-spacing: -0.02em;
}

.group-modal p {
  font-size: var(--text-base);
  font-weight: var(--font-weight-normal);
  color: var(--color-text-secondary);
  margin-bottom: var(--space-6);
  line-height: 1.5;
}

.group-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
}

.group-form input {
  padding: var(--space-4);
  border: 1.5px solid var(--color-border);
  border-radius: var(--radius-md);
  font-family: var(--font-family);
  font-size: var(--text-base);
  font-weight: var(--font-weight-medium);
  color: var(--color-text-primary);
  background-color: var(--color-surface-elevated);
  box-shadow: var(--shadow-xs);
  transition: all var(--transition-base);
  min-height: var(--tap-target-comfortable);
}

.group-form input:focus {
